
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #FFF8E1; /* Light cream background */
}

.logo {
  font-size: 4rem;
  font-weight: bold;
  color: #FFC107; /* Amber */
  margin-bottom: 2rem;
}

.button {
  background-color: red; /* Red */
  color: white;
  padding: 2rem 4rem;
  border: none;
  border-radius: 8px;
  font-size: 2.5rem;
  margin-bottom: 1rem;
  text-decoration: none;
  text-align: center;
  width: 80%;
  max-width: 600px;
}
