/* src/styles/FoundWordLine.css */
.line-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* Allows clicks to pass through to the grid */
  z-index: 1; /* Ensure lines are behind the letters */
}

.found-word-line {
  stroke: var(--line-color, #4caf50); /* Default to green if var is not set */
  stroke-width: 10; /* Adjust thickness as needed */
  stroke-linecap: round; /* Rounded ends for a smoother look */
  opacity: 0.6; /* Make it slightly transparent */
}
